import render from './render.js'

/**
 * È™åËØÅÂΩìÂâçÁöÑÊï∞ÊçÆ‰∏≠ÁöÑÂ±ûÊÄßÂÄºÊòØÂê¶ÂèëÁîü‰∫ÜÊîπÂèò
 * 
 * ‰∏Ä‰∏™‰∏Ä‰∏™ÂØπÊØîÂΩìÂâçÂ±ûÊÄßÊòØÂê¶Áº∫Â§±Ôºå‰ª•Êñ∞ÂØπË±°È™åËØÅÊóßÂØπË±°
 */
const diffattrs = (oldAttrs, newAttrs) => {
    let patchs = [];

    for (const k in oldAttrs) {
        if (!(k in newAttrs)) { //ÊïàÈ™åÊñ∞ÂØπË±°‰∏≠ÊòØÂê¶ËøòÊúâÊóßÂØπË±°ÁöÑÂ±ûÊÄßÔºåÂ¶ÇÊûúÊñ∞ÂØπË±°‰∏≠Ê≤°ÊúâËøô‰∏™Â±ûÊÄßÔºåÂàôÁî®Êà∑ÂéªÊéâ‰∫ÜÂΩìÂâçÂ±ûÊÄßÔºåÂàôÂπ≤ÊéâÂΩìÂâçÂ±ûÊÄß
            patchs.push(
                $node => {
                    $node.removeAttribute(k);
                    return $node;
                }
            )
        }
    }

    for (const [k, v] of Object.entries(newAttrs)) {
        if (!(k in oldAttrs)) { //ÊïàÈ™åÊóßÂØπË±°‰∏≠ÊòØÂê¶‰øùÂáΩÊñ∞ÂØπË±°ÁöÑÂ±ûÊÄßÔºåÂ¶ÇÊûúÊ≤°ÊúâÔºåÂàôËÆæÁΩÆÊñ∞ÂØπË±°ÁöÑÂ±ûÊÄß
            patchs.push(
                $node => {
                    $node.setAttribute(k, v);
                    return $node;
                }
            )
        }
    }

    return $node => { //ËøîÂõûÂΩìÂâçpatch ÈõÜÂêà
        for (const patch of patchs) { //‰æøÂà©ÊâÄÊúâÁöÑÊìç‰ΩúÊñπÊ≥ï Â§ÑÁêÜ$node
            patch($node);
        }
        return $node;
    }

}
/**
 * ÊØîÂØπÊï∞ÊçÆ‰∏≠ÁöÑchildrenÂÄºÊòØÂê¶ÂèëÁîü‰∫ÜÊîπÂèò
 * 
 * ÂåÖÊã¨ËäÇÁÇπÂêç Â±ûÊÄßÂÄº ÂÑøÂ≠êËäÇÁÇπ
 *  
 * ÊòéÁ°Æ‰∏ÄÁÇπÔºö
 *        1. È¶ñÂÖàÊàë‰ª¨childrenÊï∞ÁªÑ‰∏≠ÁöÑÊï∞ÊçÆÂÖ∂ÂÆûÂ∞±ÊòØ‰∏Ä‰∏™ËäÇÁÇπÁöÑÂ§ßÈõÜÂêàÔºåÈúÄË¶ÅÁî®Âà∞ÈÄíÂΩíÂ§ÑÁêÜÊØè‰∏ÄÊù°Êï∞ÊçÆ
 *        2. Â≠êËäÇÁÇπÁöÑÊï∞ÊçÆÂøÖÈ°ªÁî±Áà∂ËäÇÁÇπÊù•Êé•Áõò
 *        3. ÂØπÊØîÊóßËäÇÁÇπÊòØÂê¶Áõ∏ÂêåÔºåÁõ¥Êé•ÈÄíÂΩídiffÊñπÊ≥ï
 *        4. Áõ∏ÂØπÊØîÊóßËäÇÁÇπÔºåÊñ∞ËäÇÁÇπÂ§öÂá∫Êï∞ÊçÆÂàôÊòéÊòæ‰∏∫Êñ∞Â¢ûÁöÑÔºåÊàë‰ª¨Áõ¥Êé•ÊèíÂÖ•Âà∞ÊåáÂÆöËäÇÁÇπ‰∏ã            
 */
const diffchildren = (oldChild, newChild) => {
    let childPatches = [];

    if(oldChild.length!=newChild.length&&newChild.length==0){       //ÁßªÈô§‰∫ÜËäÇÁÇπ
        childPatches.push(
            $node=>{
                $node.remove();
                return $node
            }
        )

    }

    for (let i = 0; i < Math.min(oldChild.length, newChild.length); i++) { //Êàë‰ª¨Áõ¥Êé•ÊãøÊóßÊï∞ÊçÆÂíåÊñ∞Êï∞ÊçÆÂØπÊØî„ÄÇÂú®‰∏éÊóßÊï∞ÊçÆËäÇÁÇπÈïøÂ∫¶Áõ∏ÂêåÁöÑÊÉÖÂÜµ‰∏ãÔºå‰∏çÁÆ°ÊòØÊñ∞Â¢ûÁöÑËøòÊòØ‰øÆÊîπ‰∫ÜÂ±ûÊÄßÁöÑ„ÄÇÂÖ®ÈÉ®‰ΩøÁî®diffÂØπÊØîÔºåÂ∑ÆÂºÇÁõ¥Êé•ËøîÂõûpatch
        childPatches.push(diff(oldChild[i], newChild[i]))
    }

    let newChildPatchs = [] //Êñ∞ÂØπË±°ÊèíÂÖ•ËäÇÁÇπ
    for (const newChild of newChild.slice(oldChild.length)) { //Â§ö‰ΩôÁöÑËäÇÁÇπÁõ¥Êé•ÊèíÂÖ•Áà∂ËäÇÁÇπ‰∏≠
        newChildPatchs.push(
            $parent => {
                $parent.appendChild(render(newChild))
                return $parent
            }
        )
    }

    return $parent => {

         $parent.childNodes.forEach(($child, index) => { //Áõ¥Êé•ÊâæÂà∞ÊóßËäÇÁÇπ‰∏≠ÁöÑÂÑøÂ≠êËäÇÁÇπÔºåÂπ∂Â§ÑÁêÜÂ∑ÆÂºÇ„ÄÇ
            childPatches[index]($child);
        })

        for (const patch of newChildPatchs) {
            patch($parent)
        }
        return $parent;
    }

}
/**
 * ÁÆóÊ≥ïÂØπÊØîÊ†∏ÂøÉÊñπÊ≥ï
 * 
 * ÊãÜÂàÜÂØπÊØîÂ±ûÊÄßÂèäËäÇÁÇπ ÈÄíÂΩí
 * 
 * ËøîÂõûÂ§ÑÁêÜÂΩìÂâçÊï∞ÊçÆÁöÑÊñπÊ≥ï patch
 */
const diff = (oldVTree, newVTree) => {
    if (oldVTree == undefined) { //ÂÖ®ÈÉ®Êï∞ÊçÆÈÉΩ‰∏çÂ≠òÂú®ÁöÑÊÉÖÂÜµÔºådomÂ∞Ü‰ºöÊ∂àÂ§±
        return $node => {
            $node.remove(); //Áõ¥Êé•Âà†Èô§ÂΩìÂâçdomÊ®°Âûã ËøîÂõûundefined
            return undefined
        }
    }

    if (oldVTree.tagName !== newVTree.tagName) { //ÂΩìÂâçvtree ËäÇÁÇπÂèëÁîü‰∫ÜÂèòÂåñ
        return $node => {
            const $nodeEl = render(newVTree); //Áõ¥Êé•ÈááÁî®ÂΩìÂâçÊúÄÊñ∞ÁöÑvtree ÂàõÂª∫‰∏Ä‰∏™ÁúüÂÆûdomÊ®°Âûã
            $node.replaceWith($nodeEl); //ÊõøÊç¢ÂΩìÂâçÁöÑdomÊ®°Âûã
            return $nodeEl
        }
    }

    if (typeof oldVTree == "string" ||
        typeof newVTree == "string") { //Â¶ÇÊûúÂΩìÂâçËäÇÁÇπÂè™ÊòØ‰∏Ä‰∏™ÊñáÊú¨ËäÇÁÇπ,ÊñáÊú¨ËäÇÁÇπÊàë‰ª¨Â∞±Ê≤°ÂøÖË¶ÅÂØπÊØîÂ±ûÊÄß‰∫ÜÔºåÁõ¥Êé•ËøîÂõûÂΩìÂâçÂèòÂåñÁöÑtree
        if (oldVTree == newVTree) {
            return $node => $node //Áõ¥Êé•ÊãøÁî®ÊúÄÊñ∞Êï∞ÊçÆÂàõÂª∫ÊàêÁöÑdomÊ®°Âûã
        } else { //‰∏ç‰∏ÄÊ†∑ÁöÑÊÉÖÂÜµ‰∏ã Êàë‰ª¨Áõ¥Êé•‰ΩøÁî®ÊúÄÊñ∞ÁöÑÊï∞ÊçÆÂàõÂª∫‰∏Ä‰∏™ÊñáÊú¨ËäÇÁÇπ
            return $node => {
                const $nodeEl = render(newVTree); //Áõ¥Êé•ÈááÁî®ÂΩìÂâçÊúÄÊñ∞ÁöÑvtree ÂàõÂª∫‰∏Ä‰∏™ÁúüÂÆûdomÊ®°Âûã
                $node.replaceWith($nodeEl); //ÊõøÊç¢ÂΩìÂâçÁöÑdomÊ®°Âûã
                return $nodeEl
            }
        }

    }

    //‰∏âÁßçÊÉÖÂÜµÊéíÈô§ÁöÑÊÉÖÂÜµ‰∏ã Êàë‰ª¨ÂØπÊØîÂ±ûÊÄß

    const patchAttrs = diffattrs(oldVTree.attrs, newVTree.attrs);
    const patchChildren = diffchildren(oldVTree.children, newVTree.children);

    return $node => {
        patchAttrs($node);
        patchChildren($node);
        return $node;
    }
}
export default diff;