import './style/base.scss';
import girl from '../static/images/girl.jpg';

import createElement from './vdom/createElemt.js';
import render from './vdom/render.js';
import mount from './vdom/mount.js';
import diff from './vdom/diff.js';

let count = 0;
const createApp = (count) => createElement('div', {
    attrs: {
        id: "hhyang",
        name: "hhyang"
    },
    children: [
        createElement('div', {
            children: [
                createElement('h1', {
                    children: [
                        `ÊàëÊòØ‰Ω†Áà∏Áà∏${count}`
                    ]
                }),
            ]
        }),
        createElement('input', {
                attrs: {
                    type: "text"
                }
            }),
            String(count),
            createElement('img', {
                attrs: {
                    src: girl,
                },
            })
    ]
});

let oldApp = createApp(count);

let $rootEl = mount(render(oldApp), document.getElementById('root')); //ÊåÇËΩΩÁúüÂÆûdomÂêéÂπ∂ËøîÂõûÂΩìÂâçÁöÑdom;



setInterval(() => {
    count++;
    let newApp = createApp(count);

    let patch = diff(oldApp, newApp); //ÂØπÊØî‚ÄùÊï∞ÊçÆ‚ÄúÊâÄÊúâÁöÑ‰∏çÂêå‰πãÂ§ÑÔºåÂπ∂ËøîÂõû‰∏Ä‰∏™patchÊñπÊ≥ïÂ§ÑÁêÜdom

    $rootEl = patch($rootEl);

    oldApp = newApp;
}, 2000)